@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
    @include edgtfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .edgtf-comment-holder-inner {
        padding: 64px 0 20px;
    }
    
    .edgtf-comments-title {
        position: relative;
        margin-bottom: 21px;
        padding-left: 20px;
        &:before {
            position: absolute;
            top: 5px;
            left: 0;
            display: block;
            content: '';
            height: calc(100% - 11px);
            width: 3px;
            background-color: $first-main-color;
        }
    }

    .edgtf-comment {

        &.edgtf-no-avatar {

            .edgtf-comment-text {
                padding: 0;
            }
        }
    }

    .edgtf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;
        overflow: hidden;

        li {
            @include edgtfRelativeHolderLayout();
            margin: 31px 0 0;
            background-color: $additional-background-color;
            padding: 0;
            box-sizing: border-box;
        }

        > li {
            margin-top: 10px;
            padding: 30px 42px 30px 30px;
            box-shadow: $default-box-shadow;
            &:first-child {
                margin-top: 0;
            }
        }

        .children {
            position: relative;
            margin-top: 28px;
            padding: 0 0 0 89px;
            &:before {
                content: '';
                height: 2px;
                width: 300%; // hacking border
                background-color: #120a17;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }

    .edgtf-comment-image {
        position: relative;
        top: 0;
        left: 0;
        width: 70px;
        height: 70px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .edgtf-comment-text {
        padding: 0 0 0 108px;
        min-height: 70px;

        .edgtf-comment-info {
            @include edgtfRelativeHolderLayout();
            margin: 7px 0 4px;
            width: auto;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: right;
            margin-left: 10px;
            font-size: 17px;
            font-family: $default-heading-font;
            color: #fff;
            font-weight: 600;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            margin-top: -6px; /* hack */

            &:hover {
                color: $default-heading-color;
            }

            &:after {
                position: relative;
                top: 1px;
                content: "\f30f";
                margin-left: 5px;
                font-family: 'Ionicons';
                color: inherit;
                font-size: 18px;
                line-height: inherit;
                font-weight: normal;
                font-style: normal;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }

        .edgtf-comment-name {
            float: left;
            margin: 0;
        }

        .edgtf-comment-date {
            margin: 0;
            font-size: 14px;
            text-transform: uppercase;
            font-family: $default-heading-font;
            color: #908d92;
            letter-spacing: 0.1em;
            font-weight: 600;
        }

        .edgtf-text-holder p {
            margin: 7px 0 17px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.edgtf-comment-form {
    @include edgtfRelativeHolderLayout();
    margin: 55px 0 40px;
    clear: both;

    @include ipad-landscape {

		.edgtf-sidebar-enabled & {
			margin-bottom: 100px;
		}
	}

    .comment-reply-title {
        @include edgtfH3();
        margin: 0 0 21px;

        position: relative;
        padding-left: 20px;
        &:before {
            position: absolute;
            top: 5px;
            left: 0;
            display: block;
            content: '';
            height: calc(100% - 11px);
            width: 3px;
            background-color: $first-main-color;
        }
    }

    .comment-form-cookies-consent {
        margin: 0 0 10px;
        input {
            margin: 0 15px 0 0;
            position: relative;
            top: 1px;
        }
    }

    .logged-in-as {
        margin: 0 0 8px;
    }

    .edgtf-btn-stripe {
        &.edgtf-btn-solid:not(.edgtf-btn-custom-hover-bg):hover {
            background-color: #fd3147 !important;
        }
        
        .edgtf-btn-bg-holder {
            display: none;
        }
    }
}

@include ipad-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .edgtf-comment-holder {

        .edgtf-comment-list {

            .children {
                padding: 0;
            }
        }

        .edgtf-comment-text {

            .edgtf-comment-info {
                display: block;
            }

            .edgtf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .edgtf-comment-holder {

        .edgtf-comment-image {
            float: none;
        }

        .edgtf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

#respond textarea,
.edgtf-style-form textarea {
    height: 150px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

.wpcf7-form {

	div.wpcf7-validation-errors,
	div.wpcf7-acceptance-missing,
	div.wpcf7-spam-blocked,
	div.wpcf7-aborted,
	div.wpcf7-mail-sent-ok,
	div.wpcf7-mail-sent-ng {
		border: 2px solid $first-main-color;
	}

	span.wpcf7-not-valid-tip {
		color: $second-main-color;
        padding-bottom: 10px;
	}
    
    .wpcf7-validation-errors {
        margin: 1em 0.5em 1em;
    }
}

.edgtf-cf7-newsletter {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .edgtf-section {

        &.edgtf-left {
            width: calc(100% - 229px);
            
            .wpcf7-not-valid-tip {
                padding: 12px 0 0;
            }
        }

        &.edgtf-right {
            width: auto;
        }
    }

    .wpcf7-form-control {

        &.wpcf7-text {
            margin: 0;
            background-color: #261632;
            @include edgtfButtonClipPathBottomLeft();
        }

        &.wpcf7-submit {
            padding: 13px 61px 14px 61px;
            background-color: $custom-color;
            @include edgtfButtonClipPathTopRight();

            &:hover {
                background-color: $custom-color;
            }

            &.edgtf-btn-solid {

                .edgtf-btn-bg-holder {
                    background-color: $first-main-color;
                }
            }
        }
    }

    .ajax-loader {
        position: absolute;
        top: 7px;
        right: 7px;
    }

    @include phone-landscape {
        justify-content: center;
        flex-direction: column;
        text-align: center;

        .edgtf-section {

            &.edgtf-left,
            &.edgtf-right {
                width: 100%;

                > div {
                    width: 100%;
                }
            }
        }

        .wpcf7-form-control {

            &.wpcf7-text,
            &.wpcf7-submit {
                width: 100%;
            }

            &.wpcf7-text {
                margin-bottom: 20px;
                clip-path: none;
            }

            &.wpcf7-submit {
                @include edgtfButtonClipPathMain();
            }
        }
    }
}

.edgtf-cf7-subscribe-custom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 22px 10px 37px;
    background-color: $additional-background-color;
    box-shadow: $default-box-shadow;

    .edgtf-section {

        &.edgtf-left {
            width: 53%;
        }

        &.edgtf-right {
            display: flex;
            align-items: center;
            width: 47%;
        }
    }

    .wpcf7-form-control {

        &.wpcf7-text {
            margin: 0;
            background-color: rgba(0, 0, 0, .3);
            @include edgtfButtonClipPathBottomLeft();
        }

        &.wpcf7-submit {
            padding: 13px 64px;
            @include edgtfButtonClipPathTopRight();
        }
    }

    .ajax-loader {
        position: absolute;
        top: 7px;
        right: 7px;
    }

    @include ipad-landscape {
        justify-content: center;
        flex-direction: column;
        text-align: center;
        padding: 10px 10%;

        .edgtf-section {

            &.edgtf-left,
            &.edgtf-right {
                width: 100%;
            }

            &.edgtf-right {
                justify-content: center;
                margin-bottom: 25px;
            }
        }
    }

    @include phone-landscape {

        .edgtf-section {

            &.edgtf-right {
                flex-direction: column;

                > div {
                    width: 100%;
                }
            }
        }

        .wpcf7-form-control {

            &.wpcf7-text,
            &.wpcf7-submit {
                width: 100%;
            }

            &.wpcf7-text {
                margin-bottom: 25px;
                clip-path: none;
            }

            &.wpcf7-submit {
                @include edgtfButtonClipPathMain();
            }
        }
    }

    @include phone-portrait {
        padding: 10px 7%;
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
