/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.edgtf-slider-is-moving {
        .owl-item {
            .edgtf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.edgtf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.edgtf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            transform: translateY(-50%);
            transition: color .2s ease-out;

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;
                transition: .5s cubic-bezier(0.2, 1.22, 0.37, 0.99);

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 30px;

            &:hover {

                span {
                    transform: translateX(-3px);
                }
            }
        }

        .owl-next {
            right: 30px;

            &:hover {

                span {
                    transform: translateX(3px);
                }
            }
        }

        @include phone-portrait {
            display: none;
        }
    }

    .owl-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 16px 0 0;
        z-index: 1;

        .owl-dot {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 10px;
            margin: 0 4px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                position: absolute;
                display: inline-block;
                vertical-align: middle;
                width: 6px;
                height: 6px;
                background-color: #fff;
                border: 2px solid transparent;
                border-radius: 3em;
                box-sizing: border-box;
                transition: all .2s ease-in-out;
            }

            &:hover,
            &.active {

                span {
                    background-color: transparent;
                    border: 2px solid #fff;
                    width: 10px;
                    height: 10px;

                    .edgtf-ms-explorer & {
                        top: calc(50% + 3px);
                        transform: translateY(-50%);
                    }
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .edgtf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .edgtf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .edgtf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

.edgtf-pag-inside-slider {

    .edgtf-owl-slider {

        .owl-dots {
            position: absolute;
            bottom: 25px;
            left: 50%;
            transform: translateX(-50%);
        }
    }

    &.edgtf-has-shadow {

        .edgtf-owl-slider {

            .owl-dots {
                bottom: 65px;
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .edgtf-wrapper {
        padding: 0 !important;
    }

    .edgtf-content {
        height: 100vh;
    }

    &.admin-bar {
        .edgtf-content {
            height: calc(100vh - 32px);
        }
    }

    .edgtf-content-inner {
        @include edgtfTableLayout();
    }

    .edgtf-page-not-found {
        @include edgtfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .edgtf-404-title {
            margin: 0;
        }

        .edgtf-404-subtitle {
            margin: 35px 0 0;
        }

        .edgtf-404-text {
            margin: 10px 0 0;
        }

        .edgtf-btn {
            margin: 30px 0 0;

            &.edgtf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
		.edgtf-content {
			height: 100%;
			min-height: 100vh;
		}

		&.admin-bar {
			.edgtf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}

		.edgtf-page-not-found {
			padding: 14% 10%;
		}
	}

	@include ipad-portrait {
		.edgtf-page-not-found {
			padding: 35% 10%;
		}
	}

	@include phone-landscape {
		.edgtf-page-not-found {
			padding: 24% 10%;
		}
	}

	@include phone-portrait {
		.edgtf-page-not-found {
			padding: 35% 10% 30%
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
    position: fixed;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    clip-path: polygon(0 0,calc(100% - 13px) 0,100% 13px,100% 100%,13px 100%,0 calc(100% - 13px));
    -webkit-clip-path: polygon(0 0,calc(100% - 13px) 0,100% 13px,100% 100%,13px 100%,0 calc(100% - 13px));

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    > span {
        position: relative;
        display: block;
        background-color: transparent;
        border: 2px solid $first-main-color;
        width: 40px;
        height: 40px;
        line-height: 40px;
        font-size: 18px;
        text-align: center;
        color: #fff;
        overflow: hidden;
        transition: .2s;

        .edgtf-btt-bg-holder {
            background-color: $first-main-color;
            position: absolute;
            display: block;
            top: 57%;
            left: -219%;
            width: 250%;
            height: 250%;
            z-index: 1;
            transform: rotate(45deg) translate3d(0, 0, 0);
            transition: .4s cubic-bezier(0.24, 0.69, 0.12, 0.9);
        }

        > i {
            z-index: 12;
            position: relative;
        }

        &:before, &:after {
            content: "";
            position: absolute;
            background-color: #6b54b6;
            width: 55px;
            height: 3px;
            transform: rotate(45deg);

            .edgtf-edge &, .edgtf-ms-explorer & {
                content: none;
            }
        }

        &:before {
            top: 0;
            right: -19px;
        }

        &:after {
            bottom: 0;
            left: -19px;
        }

        &:hover {
            background-color: transparent;
            border: 2px solid $first-main-color;

            .edgtf-btt-bg-holder {
                transition: .4s cubic-bezier(0.1, 0.48, 0.25, 0.86);
                transform: rotate(45deg) translate3d(0,-75%,0);
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {
    .edgtf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

.menu-footer-menu-container {

    ul li a {
        position: relative;
        display: block;
        color: #fff;

        &:after {
            content: '';
            position: absolute;
            bottom: -9px;
            left: 0;
            display: inline-block;
            width: 100%;
            height: 4px;
            background-color: #7228e2;
            transform-origin: left;
            transform: scaleX(0);
            transition: .3s cubic-bezier(0.47, 0.11, 0, 0.96);
        }

        &:hover {
            color: #fff !important;

            &:after {
                transform: scaleX(1);
            }
        }
    }
}

// Scrollbar styles - Webkit (Chrome, Safari)

body::-webkit-scrollbar {
    width: 8px;
}

body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
    background-color: #7228e2;
    outline: 1px solid #7228e2;
}

// Scrollbar styles - Firefox

html {
    scrollbar-color: #7228e2 #0f0b12;
    scrollbar-width: thin;
}

// Custom Cursor Styles

.edgtf-custom-cursor-enabled {

    * {
        cursor: none !important;
    }

    .edgtf-custom-cursor {
        position: fixed;
        background-color: #fff;
        width: 8px;
        height: 8px;
        top: -4px;
        left: -4px;
        border-radius: 50%;
        pointer-events: none;
        z-index: 6669999;
    
        &.edgtf-custom-cursor-1 {
    
            &:before {
                content: "\f2e3";
                position: absolute;
                top: -8px;
                left: -4px;
                font-weight: 400;
                font-family: Ionicons;
                font-size: 20px;
                font-style: normal;
                color: #fff;
                width: 20px;
                line-height: inherit;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                transform: scale(0);
                transition: .2s;
            }
        }
    
        &.edgtf-custom-cursor-2 {
            background-color: #7228e2;
            // background-color: #ff3248;
            width: 34px;
            height: 34px;
            top: -17px;
            left: -17px;
            opacity: .5;
            z-index: 6669995;
            transition: width .3s cubic-bezier(0, 0.91, 0.06, 1.47), height .3s cubic-bezier(0, 0.91, 0.06, 1.47), top .3s cubic-bezier(0, 0.91, 0.06, 1.47), left .3s cubic-bezier(0, 0.91, 0.06, 1.47);
        }
    
        &.edgtf-custom-cursor-link {
    
            &.edgtf-custom-cursor-2 {
                width: 44px;
                height: 44px;
                top: -22px;
                left: -22px;
            }
        }
    
        &.edgtf-custom-cursor-drag {
            
            &.edgtf-custom-cursor-1 {
                background-color: transparent;
    
                &:before {
                    transform: scale(1);
                }
            }
    
            &.edgtf-custom-cursor-2 {
                width: 44px;
                height: 44px;
                top: -22px;
                left: -22px;
            }
        }
    }
}

// Custom Contact Form Styles (Animation)

.edgtf-custom-text-area {
    position: relative;
    overflow: hidden;

    &:after {
        content: "";
        position: absolute;
        bottom: 10px;
        left: 0;
        width: 100%;
        height: 3px;
        transform-origin: left;
        transform: scaleX(0);
        background-color: $first-main-color;
        transition: .3s cubic-bezier(0.8, 0.18, 0.25, 0.69);
        pointer-events: none;
    }

    &.edgtf-custom-cf-focus {
        
        &:after {
            transform: scaleX(1);
        }
    }
}

.edgtf-custom-text-input {
    position: relative;
    overflow: hidden;

    &:after {
        content: "";
        position: absolute;
        bottom: 10px;
        left: 5px;
        width: 97.5%;
        height: 3px;
        transform-origin: left;
        transform: scaleX(0);
        background-color: $first-main-color;
        transition: .3s cubic-bezier(0.8, 0.18, 0.25, 0.69);
        pointer-events: none;
    }

    &.edgtf-custom-cf-focus {
        
        &:after {
            transform: scaleX(1);
        }
    }
}

.edgtf-cf7-newsletter {

    .edgtf-custom-text-input {
        
        &:after {
            bottom: 0;
            left: 20px;
            background-color: $second-main-color;
        }

        @include phone-landscape {
            
            &:after {
                bottom: 20px;
                left: 0;
                width: 100%;
            }
        }
    }
}

.edgtf-cf7-custom-form {

    .edgtf-btn-stripe.edgtf-btn-huge .edgtf-btn-bg-holder {

        @include ipad-landscape {
            left: -91%;
        }

        @include ipad-portrait {
            left: -124%;
        }

        @include phone-landscape {
            left: -154%;
        }
    }
}