/*------------------------------------------------------------------
 * This is main css file for theme. It is broken into following sections:
 * 1.  Global styles and reset
 * 2.  Widgets styles
 * 3.  Plugins styles
 * 4.  Blog styles
 * 5.  Footer styles
 * 6.  Header styles
 * 7.  Side area styles
 * 8.  Search styles
 * 9.  Title area styles
 * 10. Shortcodes styles
