s /* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */
aside.edgtf-sidebar {
	position: relative;
	z-index: 11;
	margin: 0 0 60px;
}

.wpb_widgetised_column {
	position: relative;
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

/*
* $title-adjusted-size variable is default body ( line-height - font-size ) / 2 + 3 where 3 is temporary value, it's half size of widget title line height minus font size,
* $wp-widgets-map input-fields-... variables are for select and for search elements
*/
$title-adjusted-size: (26 - 12) / 2 + 3;

$wp-widgets-map: (
		widget-margin: 43,
		widget-title-margin: 27,
		widget-title-adjusted-margin: $title-adjusted-size,
		widget-title-color: null,
		list-item-bottom-margin: 19,
		text-color: null,
		link-color: $default-heading-color,
		link-hover-color: $first-main-color,
		list-link-color: null,
		list-link-hover-color: null,
		input-fields-height: $input-height,
		input-fields-side-padding: $input-horizontal-padding,
		input-fields-text-size: 15,
		input-fields-text-line-height: 24,
		input-fields-text-color: $input-text-color,
		input-fields-focus-text-color: $input-text-color,
		input-fields-background-color: $additional-background-color,
		input-fields-focus-background-color: $additional-background-color,
		input-fields-border-color: transparent,
		input-fields-focus-border-color: transparent,
		input-fields-button-color: $default-heading-color,
		input-fields-button-hover-color: $first-main-color
);

$wp-widgets-area-map: (
		'': (),
		'.edgtf-top-bar' : (
				widget-margin: 0,
				widget-title-color: #fff,
				text-color: #fff,
				link-color: #fff
		),
		'.edgtf-page-footer': (
				widget-title-margin: 37,
				widget-title-color: #fff
		),
		'.edgtf-footer-top-holder': (
				text-color: #ccc,
				link-color: #ccc,
				link-hover-color: #fff,
				input-fields-border-color: #ccc,
				input-fields-focus-border-color: $first-main-color
		),
		'.edgtf-footer-bottom-holder': (
				text-color: #3c3556,
				link-color: #fff,
				link-hover-color: #fd3147,
				input-fields-border-color: #fff,
				input-fields-focus-border-color: #3c3556
		),
		'.edgtf-side-menu': (
				widget-title-color: #fff,
				text-color: #fff,
				link-color: #fff
		)
);

@each $key, $values in $wp-widgets-area-map {
	$wp-widgets-map-merged: map-merge($wp-widgets-map, $values);

	#{$key} .widget {
		margin: 0 0 #{map-get($wp-widgets-map-merged, 'widget-margin')}px;

		@if (map-get($wp-widgets-map-merged, 'text-color') != null) {
			color: map-get($wp-widgets-map-merged, 'text-color');
		}

		&:last-child {
			margin-bottom: 0 !important;
		}

		.edgtf-widget-title {
			margin: 0 0 #{map-get($wp-widgets-map-merged, 'widget-title-margin')}px;

			@if (map-get($wp-widgets-map-merged, 'widget-title-color') != null) {
				color: map-get($wp-widgets-map-merged, 'widget-title-color');
			}

			position: relative;
			padding-left: 20px;

			&:before {
				position: absolute;
				top: 5px;
				left: 0;
				display: block;
				content: '';
				height: calc(100% - 11px);
				width: 3px;
				background-color: $first-main-color;
			}

			a {
				color: inherit;

				&:hover {
					color: map-get($wp-widgets-map-merged, 'link-hover-color');
				}
			}
		}

		@if (map-get($wp-widgets-map-merged, 'link-color') != null) {
			a {
				color: map-get($wp-widgets-map-merged, 'link-color');

				@if (map-get($wp-widgets-map-merged, 'link-hover-color') != null) {
					&:hover {
						color: map-get($wp-widgets-map-merged, 'link-hover-color');
					}
				}
			}
		}

		ul:not(.edgtf-shortcode-list) {
			padding: 0;
			margin: 0;
			list-style: none;

			li {
				margin: 0 0 #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px;

				&:last-child {
					margin-bottom: 0;
				}

				@if (map-get($wp-widgets-map-merged, 'list-link-color') != null) {
					a {
						color: map-get($wp-widgets-map-merged, 'list-link-color');

						@if (map-get($wp-widgets-map-merged, 'list-link-hover-color') != null) {
							&:hover {
								color: map-get($wp-widgets-map-merged, 'list-link-hover-color');
							}
						}
					}
				}

				ul {
					margin-top: #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px;
					margin-left: #{map-get($wp-widgets-map-merged, 'list-item-bottom-margin')}px;
				}
			}
		}

		&.widget_archive,
		&.widget_categories,
		&.widget_text {

			select {
				width: 100%;
				max-width: 100%;
				height: #{map-get($wp-widgets-map-merged, 'input-fields-height')}px;
				margin: #{map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px 0 0;
				padding: #{(map-get($wp-widgets-map-merged, 'input-fields-height') - map-get($wp-widgets-map-merged, 'input-fields-text-line-height')) / 2}px #{map-get($wp-widgets-map-merged, 'input-fields-side-padding')}px;
				font-family: inherit;
				font-size: #{map-get($wp-widgets-map-merged, 'input-fields-text-size')}px;
				line-height: #{map-get($wp-widgets-map-merged, 'input-fields-text-line-height')}px;
				font-style: inherit;
				font-weight: inherit;
				color: map-get($wp-widgets-map-merged, 'input-fields-text-color');
				background-color: map-get($wp-widgets-map-merged, 'input-fields-background-color');
				border: 1px solid #{map-get($wp-widgets-map-merged, 'input-fields-border-color')};
				border-radius: 0;
				outline: none;
				box-sizing: border-box;
			}

			.select2-container--default .select2-selection--single {
				background-color: $default-background-color;
			}
		}

		&.widget_calendar {
			width: 100%;

			#today {
				color: #fff;
				background-color: map-get($wp-widgets-map-merged, 'link-hover-color');

				a {
					color: inherit;
				}
			}

			table th,
			caption {
				color: $default-heading-color;
			}

			caption {
				margin-top: 5px;
			}

			tfoot {
				@if (map-get($wp-widgets-map-merged, 'link-color') != null) {
					a {
						color: map-get($wp-widgets-map-merged, 'link-color');

						&:hover {
							color: map-get($wp-widgets-map-merged, 'link-hover-color');
						}
					}
				}
			}
		}

		&.widget_text {

			img {
				display: block;
			}
		}

		&.widget_rss {

			ul {

				li {
					margin-bottom: 30px;

					&:last-child {
						margin-bottom: 0;
					}
				}

				.rsswidget {
					display: block;
					@include edgtfHeadingStyle(h4, false);
				}

				.rss-date {
					padding: 0 21px 0 0;
					color: $first-main-color;
					font-size: 14px;
					font-family: $default-heading-font;
					font-weight: 600;
					line-height: 1.43em;
					letter-spacing: 0.1em;
					text-transform: uppercase;
				}

				.rssSummary {
					margin-top: 5px;
				}

				cite {
					display: block;
					margin-top: 5px;
				}
			}
		}

		&.widget_search {
			&.widget_block {
				position: relative;

				.wp-block-search__button-inside {
					position: relative;

					button {
						position: absolute;
						top: 0;
						right: 0;
						width: 86px;
						text-align: center;
						color: map-get($wp-widgets-map-merged, 'input-fields-button-color');
						background-color: transparent;
						border: 0;
						cursor: pointer;
						transition: color .2s ease-out;
						display: flex;
						align-items: center;
						justify-content: center;

						&:hover {
							color: map-get($wp-widgets-map-merged, 'input-fields-button-hover-color');
						}

						svg {
							path {
								stroke: #fff;
							}
						}

						&:before {
							display: none;
						}
					}
				}
			}

			.edgtf-widget-title {
				margin-bottom: #{map-get($wp-widgets-map-merged, 'widget-title-margin') + map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px;
			}

			.input-holder {
				@include edgtfRelativeHolderLayout();
				box-shadow: $default-box-shadow;
			}

			input,
			button {
				height: #{map-get($wp-widgets-map-merged, 'input-fields-height')}px;
				padding: #{(map-get($wp-widgets-map-merged, 'input-fields-height') - map-get($wp-widgets-map-merged, 'input-fields-text-line-height')) / 2}px 0;
				font-family: inherit;
				font-weight: inherit;
				line-height: #{map-get($wp-widgets-map-merged, 'input-fields-text-line-height')}px;
				border-radius: 0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
			}

			input {
				@include edgtfRelativeHolderLayout();
				padding-left: #{map-get($wp-widgets-map-merged, 'input-fields-side-padding')}px;
				padding-right: 84px;
				font-size: #{map-get($wp-widgets-map-merged, 'input-fields-text-size')}px;
				color: map-get($wp-widgets-map-merged, 'input-fields-text-color');
				background-color: map-get($wp-widgets-map-merged, 'input-fields-background-color');
				border: 1px solid map-get($wp-widgets-map-merged, 'input-fields-border-color');
				transition: border-color .2s ease-out;
				@include edgtfButtonClipPathOtherSide();

				&:focus {
					color: map-get($wp-widgets-map-merged, 'input-fields-focus-text-color');
					background-color: map-get($wp-widgets-map-merged, 'input-fields-focus-background-color');
					border-color: map-get($wp-widgets-map-merged, 'input-fields-focus-border-color');
				}

				@include edgtfPlaceholder {
					color: inherit;
				}
			}

			button {
				position: absolute;
				top: 0;
				right: 0;
				width: 86px;
				text-align: center;
				color: map-get($wp-widgets-map-merged, 'input-fields-button-color');
				background-color: transparent;
				border: 0;
				cursor: pointer;
				transition: color .2s ease-out;

				display: flex;
				align-items: center;
				justify-content: center;

				&:hover {
					color: map-get($wp-widgets-map-merged, 'input-fields-button-hover-color');
				}
			}
		}

		&.widget_tag_cloud {

			.tagcloud {
				margin-top: -4px;

				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					margin: 0 12px 11px 0;
					font-family: inherit;
					font-size: 15px !important;
					line-height: 1em;
					font-weight: inherit;
					letter-spacing: inherit;
					text-transform: capitalize;
					color: map-get($wp-widgets-map-merged, 'link-color');

					&:hover {
						color: map-get($wp-widgets-map-merged, 'link-hover-color');
					}

					&:not(:last-child):after {
						color: $default-text-color;
						pointer-events: none;
						position: relative;
						content: '/';
						left: 6px;
					}
				}
			}
		}

		&.widget_media_image {
			margin-bottom: 0;
			box-shadow: none;

			.edgtf-widget-title {
				margin-bottom: #{map-get($wp-widgets-map-merged, 'widget-title-margin') + map-get($wp-widgets-map-merged, 'widget-title-adjusted-margin')}px;
			}

			a, img {
				display: block;
			}
		}

		&.widget_archive,
		&.widget_calendar,
		&.widget_pages,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_rss,
		&.widget_tag_cloud,
		&.widget_nav_menu,
		&.widget_text {
			background-color: $additional-background-color;
			padding: 23px 25px 38px;
			box-sizing: border-box;
			box-shadow: $default-box-shadow;
		}

		&.widget_archive,
		&.widget_pages,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_nav_menu {
			li {
				@include edgtfH5();
				font-family: $default-heading-font;
				color: $default-heading-color;
				font-weight: 700;
				text-transform: uppercase;

				a {
					color: $default-heading-color;

					&:hover {
						color: $first-main-color;
					}
				}
			}
		}
	}
}

.edgtf-top-bar,
.edgtf-page-header,
.edgtf-page-footer {

	.widget {

		&.widget_archive,
		&.widget_calendar,
		&.widget_pages,
		&.widget_categories,
		&.widget_meta,
		&.widget_recent_comments,
		&.widget_recent_entries,
		&.widget_rss,
		&.widget_tag_cloud,
		&.widget_nav_menu,
		&.widget_text {
			background-color: transparent;
			padding: 0;
			box-shadow: none;
		}

		&.widget_media_image {
			box-shadow: none;
		}
	}
}

.edgtf-page-footer {

	.widget {
		padding: 0 !important;
		font-family: $default-heading-font;
		font-size: 14px;
		line-height: 1.43em;
		font-weight: 600;
		text-transform: uppercase;
		letter-spacing: 0.1em;
		background-color: transparent !important;

		@include ipad-landscape {
			text-align: center;
		}

		&.widget_nav_menu {

			ul {
				display: flex;
				align-items: center;
				justify-content: space-evenly;

				.edgtf-edge &,
				.edgtf-ms-explorer &,
				.edgtf-safari & {
					justify-content: space-around;
				}

				@include phone-landscape {
					display: block;
				}

				li {
					margin: 0;
					font-size: 17px;
					font-weight: 600;
					letter-spacing: .1em;

					@include phone-landscape {
						margin: 0 0 20px;
						display: flex;
						justify-content: center;
					}
				}
			}
		}

		&.widget_media_image {

			a, img {
				display: inline-block;
			}
		}
	}
}

.edgtf-footer-bottom-holder {

	.widget {

		&.widget_text {

			@include ipad-landscape {

				h1, h2, h3, h4, h5, h6 {
					margin: 10px 0;
				}
			}
		}
	}
}

/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */

/* ==========================================================================
   Search appearance tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	display: none;
}

// IE
input[type="search"]::-ms-clear {
	display: none;
}

input[type=text]::-ms-clear {
	display: none;
}

/* ==========================================================================
   Price filter widget begin
   ========================================================================== */
// New widgets
.widget {
	&.widget_block {
		.wc-blocks-filter-wrapper {
			h3 {
				position: relative;
				padding-left: 20px;
				margin-bottom: 20px !important;

				&:before {
					position: absolute;
					top: 5px;
					left: 0;
					display: block;
					content: '';
					height: calc(100% - 11px);
					width: 3px;
					background-color: $first-main-color;
				}
			}
		}
	}

	// price filter - legacy and block common
	$slider-handle-size: 8px;
	$slider-rail-height: 5px;
	$slider-bottom-margin: 20px;
	--range-color: $first-main-color !important;

	.wc-block-components-price-slider__range-input-progress {
		--track-background: linear-gradient(to right, transparent var(--low), var(--range-color) 0, var(--range-color) var(--high), transparent 0) no-repeat 0 100% / 100% 100%;
		--range-color: #6b54b6 !important;
		/*rtl:ignore*/
		background: var(--track-background);
	}

	@mixin qodefWooSliderHandle() {
		height: 26px;
		width: 2px !important;
		background-color: $first-main-color !important;
		border-color: transparent !important;
		margin: 0 0 0 -1px !important;
		cursor: pointer;
		outline: 0 !important;
		z-index: 2;
		border-radius: 0 !important;
		transform: rotate(-45deg) translate(100%, -40%) !important;
		transform-origin: 90% 47%;


		&:hover {
			@include qodefWooSliderHandleFocus();
		}
	}

	@mixin qodefWooSliderHandleFocus() {
		background-color: $first-main-color !important;
		border-color: transparent !important;
	}

	.wc-block-components-price-slider__range-input-wrapper {
		background: #0d0711;
	}

	.wc-block-components-price-slider__range-input--max {

	}

	// price filter - block
	.wp-block-woocommerce-price-filter,
	.wp-block-woocommerce-filter-wrapper {

		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			margin-bottom: 14px;
		}
	}

	.wc-block-components-price-slider {
		display: flex;
		flex-wrap: wrap;
		margin: 0;
		flex-direction: row-reverse;
		justify-content: space-between;

		.wc-block-components-price-slider__range-input-wrapper {
			box-shadow: none;
			height: $slider-rail-height;
			margin: calc((#{$slider-handle-size} / 2)) 0;
			width: 100%;
		}

		.wc-block-components-price-slider__range-input-progress {
			height: $slider-rail-height;

		}

		// cant comma-separate these type of selectors
		.wc-block-components-price-slider__range-input {

			&::-webkit-slider-thumb {
				@include qodefWooSliderHandle();
			}

			&::-moz-range-thumb {
				@include qodefWooSliderHandle();
			}

			&::-ms-thumb {
				@include qodefWooSliderHandle();
			}

			&:focus {

				&::-webkit-slider-thumb {
					@include qodefWooSliderHandleFocus();
				}

				&::-moz-range-thumb {
					@include qodefWooSliderHandleFocus();
				}

				&::-ms-thumb {
					@include qodefWooSliderHandleFocus();
				}
			}

			&.wc-block-components-price-slider__range-input--max {
				&::-webkit-slider-thumb {
					transform-origin: 90% 25% !important;
				}

				&::-moz-range-thumb {
					transform-origin: 90% 25% !important;
				}

				&::-ms-thumb {
					transform-origin: 90% 25% !important;
				}
			}

		}

		.wc-block-components-price-slider__controls,
		.wc-block-components-price-slider__range-text {
			margin: $slider-bottom-margin 0 0 0;
			order: 100;

			input {
				margin: 0;
			}

			span {

				&:first-child {

					&:after {
						content: '';
						margin: 0 4px;
					}
				}
			}
		}

		.wc-block-components-price-slider__range-text {
			@include edgtfHeadingStyle(h6);
			line-height: 2em;
		}

		.wc-block-components-price-slider__actions {
			flex-direction: row-reverse;
			margin: $slider-bottom-margin 0 0 0;

			button {
				font-family: $default-heading-font;
				font-size: 17px;
				font-weight: 600;
				letter-spacing: .1em;
				text-transform: uppercase;
				float: right;
				padding: 0 !important;
				color: $default-heading-color !important;
				background-color: transparent !important;
				border: 0 !important;
				vertical-align: middle;
				line-height: 1.43em;
				clip-path: none !important;

				&:before {
					display: none;
				}

				&:after {
					content: "\f30f";
					position: relative;
					display: inline-block;
					font-family: "Ionicons";
					padding-left: 10px;
					font-size: 18px;
					font-weight: 400;
				}

				&:hover {
					background-color: transparent;
					color: $first-main-color;
				}
			}
		}
	}
}